import React, { useState } from 'react';
import { AppShell, Header, MantineProvider, Text, Group, Container, MediaQuery, 
  Burger, Collapse, UnstyledButton } from '@mantine/core';
import { Navigate, Route, Routes, useNavigate } from 'react-router-dom';
import logo from './studio.svg';
import Avvisi from './pages/Avvisi';
import Orari from './pages/Orari';
import Contatti from './pages/Contatti';

export default function App() {

  const [burgerOpen, setBurgerOpen] = useState(false);
  const navigate = useNavigate();

  return (
    <MantineProvider theme={{colorScheme: 'dark', fontFamily: 'Greycliff CF, sans-serif'}}>
        <AppShell
          padding='md'
          header={
            <>
            {/* Desktop version */}
            <MediaQuery smallerThan='md' styles={{display: 'none'}}>
            <Header height='20%' p='xs'>
              <Container px='xs'>
                <Group position='apart'>
                  <Group sx={{ height: '100%' }} px={20} pt={15} pl='xl' position='left'>
                    <img src={logo} alt='' style={{width: 50, position: 'relative', top: -10}}></img>
                    <Group direction='column' position='center'>
                      <Text size="xl" weight={800} color='white'>STUDIO MEDICO</Text>
                      <Text size="sm" weight={500} color='white' style={{position: 'relative', bottom: 10}}>Dott.ssa Ceo Grazia</Text>
                    </Group>
                  </Group>

                  <UnstyledButton pb='sm'
                    onClick={() => {setBurgerOpen(false); navigate('/')}}
                  >
                    <Text size="sm" weight={600} color='white' >HOME</Text>
                  </UnstyledButton>
                  <UnstyledButton pb='sm'
                    onClick={() => {setBurgerOpen(false); navigate('/orari')}}
                  >
                    <Text size="sm" weight={600} color='white' >ORARI</Text>
                  </UnstyledButton>
                  <UnstyledButton pb='sm'
                    onClick={() => {setBurgerOpen(false); navigate('/contatti')}}
                  >
                    <Text size="sm" weight={600} color='white' >CONTATTI</Text>
                  </UnstyledButton>
                  </Group>
              </Container>
            </Header>
            </MediaQuery>

            {/* Mobile version */}
            <MediaQuery largerThan='md' styles={{display: 'none'}}>
            <Header height='20%' p='xs'>
              <Container px='xs'>
                <Group position='apart'>
                  <Group sx={{ height: '100%' }} px={20} pt={15} pl='xl' position='left'>
                    <img src={logo} alt='' style={{width: 50, position: 'relative', top: -10}}></img>
                    <Group direction='column' position='center'>
                      <Text size="xl" weight={800} color='white'>STUDIO MEDICO</Text>
                      <Text size="sm" weight={500} color='white' style={{position: 'relative', bottom: 10}}>Dott.ssa Ceo Grazia</Text>
                    </Group>
                  </Group>

                  <MediaQuery largerThan='md' styles={{display: 'none'}}>
                    <Burger 
                      opened={burgerOpen}
                      onClick={() => setBurgerOpen(o => !o)}
                      size='sm'
                    />
                  </MediaQuery>
                </Group>
                <MediaQuery largerThan='md' styles={{display: 'none'}}>
                  <Collapse in={burgerOpen}>
                    <Group direction='column' pt='md'>
                      <UnstyledButton pb='xl' style={{width: '100%'}}
                        onClick={() => {setBurgerOpen(false); navigate('/')}}
                      >
                        <Text size="sm" weight={600} color='white' >HOME</Text>
                      </UnstyledButton>
                      <UnstyledButton pb='xl' style={{width: '100%'}}
                        onClick={() => {setBurgerOpen(false); navigate('/orari')}}
                      >
                        <Text size="sm" weight={600} color='white' >ORARI</Text>
                      </UnstyledButton>
                      <UnstyledButton pb='xl' style={{width: '100%'}}
                        onClick={() => {setBurgerOpen(false); navigate('/contatti')}}
                      >
                        <Text size="sm" weight={600} color='white' >CONTATTI</Text>
                      </UnstyledButton>
                    </Group>
                  </Collapse>
                </MediaQuery>
              </Container>

            </Header>
            </MediaQuery>
            </>
            
          }
          styles={(theme) => ({
            main: { backgroundColor: theme.colorScheme === 'dark' ? theme.colors.dark[8] : theme.colors.gray[0] },
          })}
        >
          <Routes>
            <Route path='/' element={<Avvisi />}/>
            <Route path='/orari' element={<Orari />}/>
            <Route path='/contatti' element={<Contatti />}/>
            <Route path='*' element={<Navigate to='/' replace />}/>
          </Routes>
        </AppShell>
    </MantineProvider>
  );
}
